import ajax from '@src/common/ajax';

const domain = window.GLOBAL_ENV.SSO_CENTER_DEFAULT_API;

const formatResult = function(result) {
  if (result.errcode) {
    return Promise.reject(result.message);
  }

  return Promise.resolve(result.data);
};

/**
 * 获取用户状态
 */
export const getBaseInfo = function() {
  return ajax({
    url: `${domain}/m/PermissionApply/permission-auth/get-user-base-info`,
  })
    .then(res => {
      return formatResult(res);
    })
    .then(data => {
      return data.base_info;
    });
};

/**
 * 获取申请的项目
 * @param {*} type
 */
export const getApplyProjectList = function(type) {
  return ajax({
    url: `${domain}/m/PermissionApply/permission-auth/get-apply-project-list`,
    method: 'post',
    data: { type },
  }).then(res => {
    return formatResult(res);
  });
};

/**
 * 获取项目审核流程
 * @param {*} data
 */
export const getFlow = data => {
  return ajax({
    method: 'POST',
    url: `${domain}/m/PermissionApply/permission-auth/get-project-approve-flow`,
    data: data,
  })
    .then(result => {
      if (result.errcode != 0) {
        return Promise.reject('请求失败');
      }

      return Promise.resolve(result.data);
    })
    .catch(err => {
      return Promise.reject(err);
    });
};

