/**
 * 撤销申请
 *
 * @param {*} apply_id 申请记录ID，必填
 * @param {*} project_id 项目ID，撤销项目时必填
 * @param {*} keyword_id 合同关键字主键ID，撤销合同时必填
 */
export const cancelApply = function(data) {
  return ajax({
    url: `${domain}/m/PermissionApply/permission-auth/cancel-apply`,
    method: 'post',
    data,
  }).then(res => {
    return formatResult(res);
  });
};
