<template>
  <div class="user-info-wrapper">
    <app-header title="个人信息" />
    <div class="user-info-content">
      <cell title="头像" class="van-hairline--bottom" @click="editAvatar">
        <template slot="right">
          <div class="avatar">
            <img v-if="userInfo.avatarUrl" :src="userInfo.avatarUrl" />
          </div>
        </template>
      </cell>
      <cell title="姓名" :isLink="false" class="van-hairline--bottom">
        <template slot="right">{{ userInfo.name }}</template>
      </cell>
      <cell title="手机号码" :isLink="false" class="van-hairline--bottom">
        <template slot="right">{{ userInfo.mobile }}</template>
      </cell>
      <!-- <cell
        title="角色"
        :isLink="false"
        class="van-hairline--bottom"
        v-if="userInfo.role.length"
      >
        <template slot="right">
          <ul class="role-list">
            <li
              class="role-item"
              v-for="(item, index) in userInfo.role"
              :key="index"
            >
              {{ item }}
            </li>
          </ul>
        </template>
      </cell> -->
      <!-- <div class="role-cell van-hairline--bottom">
        <div class="role-cell-left">角色</div>
        <div class="role-cell-right">
          <span v-if="roleList.length > 2">{{
            roleList.length
          }}</span>
          <span v-else>
            <div v-for="(item, index) in roleList" :key="index">
              {{ item.fullPath }}
            </div>
          </span>
        </div>
      </div> -->

      <cell
        title="角色"
        :isLink="roleList.length > 2"
        :border="true"
        @click="toRoleDetail"
      >
        <template slot="right">
          <span v-if="roleList.length > 2">{{ roleList.length }}</span>
          <span v-else>
            <div v-for="(item, index) in roleList" :key="index">
              {{ item.fullName || item.name }}
            </div>
          </span>
        </template>
      </cell>

      <cell
        :border="true"
        :isLink="postList.length > 2"
        @click="toPostDetail"
        title="岗位"
      >
        <template slot="right">
          <span v-if="postList.length > 2">{{ postList.length }}</span>
          <span v-else>
            <div v-for="(item, index) in postList" :key="index">
              {{ item }}
            </div>
          </span>
        </template>
      </cell>
      <cell
        :border="true"
        :isLink="orgAndContractorList.length > 2"
        @click="toOrgAnddContractorDetail"
        :title="userInfo.userType == '0' ? '所属组织' : '所属供应商'"
      >
        <template slot="right">
          <span v-if="orgAndContractorList.length > 2">{{
            orgAndContractorList.length
          }}</span>
          <span v-else>
            <div v-for="(item, index) in orgAndContractorList" :key="index">
              {{ item.name }}
            </div>
          </span>
        </template>
      </cell>
      <cell title="修改密码" @click="toPassword" />
    </div>

    <van-popup
      v-model="showAvatarSelect"
      class="avatarSelector"
      position="bottom"
    >
      <div class="actions">
        <div
          v-for="(item, index) in actions"
          :key="index"
          :class="{ 'van-hairline--bottom': !index }"
          class="action-item action-button"
          @click="onSelect(item)"
        >
          {{ item.name }}
        </div>
      </div>
      <div class="cancel action-button" @click="onCancel">
        取消
      </div>
    </van-popup>
  </div>
</template>

<script>
import Cell from '@components/app-cell';
import { getUserContext, setUserContext } from '@yl-app-jssdk/context';
import { getPrefixPath, getWebViewPath } from '@yl-app-jssdk/path';
import Photo from '@src/common/take-photo';
import UserService from '@services/user-service';
import Guid from '@yl/guid';
import {
  getUserRoleInfo,
  getUserPostInfo,
  getUserContractorInfo,
  getUserOrgInfo,
} from '@/src/api/user-state';
/** 共享对象 用于二级详情页*/
export const commonDetailObj = {
  title: '',
  list: [],
};
export default {
  components: {
    Cell,
  },

  data() {
    return {
      showAvatarSelect: false,
      userInfo: {
        avatarUrl: '',
        name: '',
        mobile: '',
        role: '',
        post: '',
        userType: '',
      },
      actions: [
        { name: '拍照', type: 1 },
        { name: '从相册选择', type: 2 },
      ],
      roleList: [],
      postList: [],
      contractorList: [],
      orgList: [],
    };
  },

  activated() {
    this.init();
  },

  computed: {
    orgAndContractorList() {
      if (this.userInfo.userType == 0) {
        return this.orgList.map(item => ({ name: item.fullPathName }));
      } else {
        return this.contractorList;
      }
    },
  },
  methods: {
    init() {
      UserService.getUserRoleInfo().then(() => {
        let user = getUserContext();
        this.userInfo.avatarUrl =
          getWebViewPath(user.avatarPath) || user.avatarUrl || '';
        this.userInfo.name = user.tenantInfo.tenantName || '';
        this.userInfo.title = '';
        this.userInfo.mobile = user.tenantInfo.mobile || '';
        this.userInfo.role = user.role;
        this.userInfo.post = user.post;
        this.userInfo.userType = user.userType;

        /** 初始化数据 */
        // this.roleList = [];
        // this.postList = [];
        // this.contractorList = [];
        // this.orgList = [];
        /** 获取完整的角色信息 */
        getUserRoleInfo({ type: 'list' }).then(res => {
          this.roleList = res;
        });
        /** 获取完整的岗位信息 */
        getUserPostInfo({ type: 'list' }).then(res => {
          // console.log(res, 'resresresresresresresresresres');
          this.postList = res;
        });
        /** 非甲方才有供应商 */
        if (user.userType != 0) {
          /** 获取完整的供应商信息 */
          getUserContractorInfo({ type: 'list' }).then(res => {
            console.log(res, 'ContractorContractorContractorContractor');
            this.contractorList = res;
          });
        } else {
          /** 获取完整的组织信息 */
          getUserOrgInfo({ type: 'list' }).then(res => {
            console.log(res, 'OrgOrgOrgOrgOrg');
            this.orgList = res;
          });
        }
      });
    },

    editAvatar() {
      this.showAvatarSelect = true;
    },

    onSelect(item) {
      let avatarId = Guid();
      let newAvatarPath = getPrefixPath('avatar');
      newAvatarPath.fullPath += avatarId + '.jpg';
      newAvatarPath.relativePath += avatarId + '.jpg';
      let newAvatarUrl;

      Photo.takeAvatar(newAvatarPath.fullPath, item.name !== '拍照')
        .then(() => {
          this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true, // 禁用背景点击
            loadingType: 'spinner',
            message: '正在上传头像...',
          });
          // TODO: 上传头像图片;
          return UserService.uploadAvatar(newAvatarPath.fullPath)
            .then(url => {
              newAvatarUrl = url;
              this.$toast.clear();
            })
            .catch(() => {
              this.$toast.clear();
              this.$toast('上传头像失败');
              return Promise.reject('上传头像失败');
            });
        })
        .then(() => {
          // 返回url和本地路径
          return {
            original: newAvatarUrl,
            localPath: newAvatarPath.fullPath + '?' + new Date().getTime(),
          };
        })
        .then(img => {
          if (img) {
            setUserContext({
              avatarUrl: img.original,
              avatarPath: img.localPath,
            });
            this.userInfo.avatarUrl =
              process.env.VUE_APP_ENV === 'dev'
                ? img.original
                : getWebViewPath(img.localPath);
            this.$toast('保存成功');
            this.showAvatarSelect = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.$toast.clear();
        });
    },

    onCancel() {
      this.showAvatarSelect = false;
    },

    toPassword() {
      this.$router.push({ name: 'password' });
    },
    /** 跳转到角色详情 */
    toRoleDetail() {
      if (this.roleList.length > 2) {
        commonDetailObj.title = '角色';
        console.log(this.roleList, 'roleList');
        commonDetailObj.list = this.roleList.map(item => ({
          name: item.fullPath,
        }));
        this.$router.push('/user/info-detail');
      }
    },
    /** 跳转到岗位详情 */
    toPostDetail() {
      if (this.postList.length > 2) {
        commonDetailObj.title = '岗位';
        commonDetailObj.list = this.postList.map(item => ({
          name: item,
        }));
        this.$router.push('/user/info-detail');
      }
    },
    /** 跳转到供应商/组织详情 */
    toOrgAnddContractorDetail() {
      if (this.orgAndContractorList.length > 2) {
        if (this.userInfo.userType == 0) {
          commonDetailObj.title = '所属组织';
        } else {
          commonDetailObj.title = '所属供应商';
        }

        commonDetailObj.list = this.orgAndContractorList;
        this.$router.push('/user/info-detail');
      }
    },
  },
};
</script>

